<template>
    <div>
        <h3 class="text-center">Naši partneri</h3>
        <br>
        <vueper-slides
                autoplay    
                class="no-shadow"
                :visible-slides="noSlides"
            
                :gap="3"
                :slide-ratio="1 / noSlides"
                :dragging-distance="200"
            >
            <vueper-slide v-for="(slide, i) in slides" :key="i" :image="slide.image" />
        </vueper-slides>
    </div>
   
</template>

<script>
import { VueperSlides, VueperSlide } from 'vueperslides'
import 'vueperslides/dist/vueperslides.css'

export default {
    components: {
        VueperSlides, VueperSlide
    },
    data: () => ({
        slides: [
            {
                image: require('@/assets/covicStill.png')
            },
            {
               image: require('@/assets/Avetinja.png')
            },
            {
                image: require('@/assets/gradskaKafana.png')
            }, 
            {
                image: require('@/assets/green.jpg')
            },
            {
                image: require('@/assets/renta.png')
            }
        ]
    }),
    computed: {
        noSlides : function() {
            if (window.innerWidth < 640) {
                return 1
            } else return 4
        }
    }
}
</script>

<style scoped>
    .vueperslide {
        background-size: 100%;
        background-repeat: no-repeat;
    }
</style>