<template>
     <div class="row featurette" ref="kontakt">
          <div class="col-md-7">
            <h4 class="text-center">Kontaktirate nas <span class="text-muted"></span></h4>
            <br>
            <div class="row">
              <div class="col-sm-12 col-md-3 broj">Perionica <br> <b>062/18-31-581</b></div>
              <div class="col-sm-12 col-md-3 broj">Office <br> <b>062/500-565</b></div>
              <div class="col-sm-12 col-md-3 broj">Office <br> <b>/??-??-???</b></div>
              <div class="col-sm-12 col-md-3 broj">Office <br> <b>/??-??-???</b></div>
            </div>
            <br>
            <form>
            <br>
            <h5 class="text-center">Pošaljite email</h5>
            <div class="form-group">
              
              <label for="ime">
                <transition name="slide-fade">
                  <span v-if="!greska.ime">Ime:</span> 
                  <i v-else>{{imegreska}}</i>
                </transition>
              </label>
              <input type="text" 
                    @click="resetGreske"
                    :class="{
                      'form-control' : true, 
                      'greska-polje': greska.ime
                    }" id="ime" 
                    v-model="ime">
            </div><br>
            <div class="form-group">
              <label for="email">
                <transition name="slide-fade">
                  <span v-if="!greska.email">Email:</span> 
                  <i v-else>{{emailgreska}}</i>
                </transition>
              </label>
              <input 
                type="email" 
                @click="resetGreske"
                :class="{
                  'form-control' : true, 
                  'greska-polje': greska.email
                  }" id="email" 
                  v-model="email">
            </div><br>
            <div class="form-group">
              <label for="poruka">
                <transition name="slide-fade">
                  <span v-if="!greska.poruka">Poruka:</span> 
                  <i v-else>Poruka je obavezna za unos</i>
                </transition>
              </label>
              <textarea 
                @click="resetGreske"
                :class="{
                  'form-control' : true, 
                  'greska-polje': greska.poruka
                  }" id="poruka" rows="3" 
                  v-model="poruka">
              </textarea>
            </div><br>
            <div class="btn btn-primary primarna-pozadina" @click="proveraForme">POŠALJI</div>
          </form>
          </div>
          <div class="col-md-5 img-desno">
            <br> <br>
            <img src="../assets/logo_glavni.png" alt="" srcset="">
            <br> <br>
            <h5 class="text-center">superpraklja@gmail.com</h5>
          </div>
    </div>
</template>

<script>
export default {
    name: "Contact",
    data: function () {
      return {
        ime: "",
        email: "",
        poruka: "",
        greska: {
          ime: false,
          email: false,
          poruka: false
        },
        emailgreska: "",
        imegreska: ""
      }
    },
    methods: {
    proveraForme: function (e) {
      if (this.ime === "") {
        this.greska.ime = true;
        this.imegreska = "Ime je obavezno za unos";
        e.preventDefault();
      } else if (this.ime.length > 15) {
        this.greska.ime = true;
        this.imegreska = "Potpis ne moze sadržati vise od 15 karaktera";
        e.preventDefault();
      }

      if (this.email === "") {
        this.greska.email = true;
        this.emailgreska = "Email je obavezan za unos";
        e.preventDefault();
      } else if (!this.pravilanEmail(this.email)) {
        this.greska.email = true;
        this.emailgreska = "Email nije pravilno unet";
        e.preventDefault();
      }

      if (this.poruka === "") {
        this.greska.poruka = true;
        e.preventDefault();
      }
    },
    pravilanEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    resetGreske: function () {
      this.greska = {
        ime: false,
        email: false,
        message: false,
      };
    },
  },
}
</script>

<style scoped>
    .btn.primarna-pozadina {
        border-color: rgba(38,45,92,1);
    }
    .greska-polje {
      border: 1px solid red;
    }
    .form-group {
      position: relative;
    }
    span {
      position: absolute;
      top: 0px;
    }
    i {
      position: absolute;
      top: 0px;
      color: red;
    }
    .slide-fade-enter-active {
      display: inline;
      transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
    }
    .slide-fade-leave-active {
      transition: all 0.3s ease;
    }
    .slide-fade-enter, .slide-fade-leave-to
    {
      transform: translateX(50px);
      opacity: 0;
    }
    .broj {
      text-align: center;
      font-size: 1.2em;
      background-color: rgb(235, 235, 235);
      padding: 10px 0px;
    }
</style>